'use client';

import { Button } from "@/components/ui/button";
import { AdvancedLessonVideo } from "@/components/video-player";
import { PlayCircle, ChevronDown, Lock } from "lucide-react";
import { getFile, parseOptions } from "@/lib/utils";
import { Collapsible, CollapsibleContent, CollapsibleTrigger } from "@/components/ui/collapsible";
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";
import { Label } from "@/components/ui/label";
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { useState } from "react";
import { curriculum } from "@/lib/data";
import { toast } from "sonner";
import { quiz } from "./sections/CourseSection";
import { Card, CardContent } from "@/components/ui/card";
import { TabsContent } from "@/components/ui/tabs";
import { Course } from "./sections/CourseSection";

export default function Curriculum({ course }: { course: Course | null }) {

    const [openSections, setOpenSections] = useState<string[]>([]);
    const [selectedAnswers, setSelectedAnswers] = useState<Record<string, string>>({});
    const [quizResults, setQuizResults] = useState<Record<string, boolean>>({});
    const [playingVideo, setPlayingVideo] = useState<string | null>(null);
    const [openQuizLesson, setOpenQuizLesson] = useState<string | null>(null);
    const [completedLessons, setCompletedLessons] = useState<string[]>(["lesson-1-1"]);

    const getPreviousLessonId = (sectionIndex: number, lessonIndex: number) => {
        if (sectionIndex === 0 && lessonIndex === 0) return null;
        let prevSectionIndex = sectionIndex;
        let prevLessonIndex = lessonIndex - 1;

        if (prevLessonIndex < 0) {
            prevSectionIndex = sectionIndex - 1;
            prevLessonIndex = curriculum[prevSectionIndex].lessons.length - 1;
        }

        return curriculum[prevSectionIndex].lessons[prevLessonIndex]?.id;
    };


    const toggleSection = (sectionId: string) => {
        setOpenSections(prev =>
            prev.includes(sectionId)
                ? prev.filter(id => id !== sectionId)
                : [...prev, sectionId]
        );
    };

    const handleQuizSubmit = (lessonId: string, quiz?: quiz) => {
        const userAnswer = selectedAnswers[lessonId];

    };

    const isLessonLocked = (sectionIndex: number, lessonIndex: number) => {
        const prevLessonId = getPreviousLessonId(sectionIndex, lessonIndex);
        if (!prevLessonId) return false;
        return !completedLessons.includes(prevLessonId);
    };

    const markLessonCompleted = (lessonId: string) => {
        setCompletedLessons((prev) => (prev.includes(lessonId) ? prev : [...prev, lessonId]));
        toast.success("Lesson marked as completed");
    };


    return (
        <TabsContent value="curriculum" className="mt-8">
            <Card>
                <CardContent className="p-6">
                    <h2 className="text-2xl font-bold text-foreground mb-6">Course Curriculum</h2>
                    <div className="space-y-4">
                        {course?.lessions?.map((lesion, sectionIndex) => (
                            <Collapsible
                                key={lesion.id}
                                open={openSections.includes(`${lesion.id}`)}
                                onOpenChange={() => toggleSection(`${lesion.id}`)}
                            >
                                <Card>
                                    <CardContent className="p-0">
                                        <CollapsibleTrigger className="w-full p-4 hover:bg-muted/50 transition-colors">
                                            <div className="flex justify-between items-center">
                                                <div className="text-left">
                                                    <h3 className="font-semibold text-foreground mb-1">
                                                        Section {sectionIndex + 1}: {lesion.title}
                                                    </h3>
                                                    <p className="text-sm text-muted-foreground">
                                                        {course?.lessions?.length} lessons
                                                    </p>
                                                </div>
                                                <ChevronDown
                                                    className={`h-5 w-5 transition-transform ${openSections.includes(`${lesion.id}`) ? "rotate-180" : ""
                                                        }`}
                                                />
                                            </div>
                                        </CollapsibleTrigger>

                                        <CollapsibleContent>
                                            <div className="border-t">
                                                {course?.sections?.map((section, sectionIndex) => (
                                                    <Collapsible
                                                        key={section.id}
                                                        open={openSections.includes(section.id)}
                                                        onOpenChange={() => toggleSection(section.id)}
                                                    >
                                                        <Card>
                                                            <CardContent className="p-0">
                                                                {/* Section Header */}
                                                                <CollapsibleTrigger className="w-full p-4 hover:bg-muted/50 transition-colors">
                                                                    <div className="flex justify-between items-center">
                                                                        <div>
                                                                            <h3 className="font-semibold text-foreground mb-1">
                                                                                Section {sectionIndex + 1}: {section.title}
                                                                            </h3>
                                                                            <p className="text-sm text-muted-foreground">
                                                                                {section.lessons.length} lessons
                                                                            </p>
                                                                        </div>
                                                                        <ChevronDown
                                                                            className={`h-5 w-5 transition-transform ${openSections.includes(section.id) ? "rotate-180" : ""
                                                                                }`}
                                                                        />
                                                                    </div>
                                                                </CollapsibleTrigger>

                                                                {/* SECTION CONTENT */}
                                                                <CollapsibleContent>
                                                                    <div className="border-t">

                                                                        {section.lessons.map((lesson, lessonIndex) => (
                                                                            <Collapsible key={lesson.id}>
                                                                                <div className="border-b last:border-b-0">

                                                                                    {/* Lesson Header */}
                                                                                    <CollapsibleTrigger className="w-full p-4 hover:bg-muted/30 transition-colors">
                                                                                        <div className="flex items-center justify-between">
                                                                                            <div className="flex items-center gap-3 text-left">
                                                                                                <PlayCircle className="h-5 w-5 text-primary" />
                                                                                                <p className="font-medium text-foreground">
                                                                                                    {lessonIndex + 1}. {lesson.title}
                                                                                                </p>
                                                                                            </div>
                                                                                            <ChevronDown className="h-4 w-4 text-muted-foreground" />
                                                                                        </div>
                                                                                    </CollapsibleTrigger>

                                                                                    {/* LESSON CONTENT */}
                                                                                    <CollapsibleContent>
                                                                                        <div className="p-4 space-y-4 bg-muted/20">

                                                                                            {/* Lesson description */}
                                                                                            <p className="text-sm text-muted-foreground">
                                                                                                {lesson.description}
                                                                                            </p>

                                                                                            {/* Video player */}
                                                                                            {isLessonLocked(sectionIndex, lessonIndex) ? (
                                                                                                <LockedLesson />
                                                                                            ) : (
                                                                                                <AdvancedLessonVideo
                                                                                                    src={getFile(lesson?.files?.[0]?.file_path || "")}
                                                                                                    lessonId={lesson.id}
                                                                                                    title={lesson.title}
                                                                                                    onComplete={markLessonCompleted}
                                                                                                    playingVideo={playingVideo}
                                                                                                    setPlayingVideo={setPlayingVideo}
                                                                                                />
                                                                                            )}

                                                                                            {/* QUIZ */}
                                                                                            {lesson.quiz && (
                                                                                                <LessonQuiz
                                                                                                    lesson={lesson}
                                                                                                    selectedAnswers={selectedAnswers}
                                                                                                    setSelectedAnswers={setSelectedAnswers}
                                                                                                    quizResults={quizResults}
                                                                                                    handleQuizSubmit={handleQuizSubmit}
                                                                                                    openQuizLesson={openQuizLesson}
                                                                                                    setOpenQuizLesson={setOpenQuizLesson}
                                                                                                />
                                                                                            )}

                                                                                        </div>
                                                                                    </CollapsibleContent>

                                                                                </div>
                                                                            </Collapsible>
                                                                        ))}

                                                                    </div>
                                                                </CollapsibleContent>

                                                            </CardContent>
                                                        </Card>
                                                    </Collapsible>
                                                ))}

                                            </div>
                                        </CollapsibleContent>
                                    </CardContent>
                                </Card>
                            </Collapsible>
                        ))}
                    </div>
                </CardContent>
            </Card>
        </TabsContent>
    )
}